import Swal from "sweetalert2"
import "../../assets/css/style.min.css"

class RegexHelper {
    // constructor(){}
    /**
     *    .
     * @param {string} selector 
     * @param {string} msg       
     * @param {boolean}   true /    false
     */
    value(selector, msg) {
        const field = document.querySelector(selector);
        const content = field.value.trim();
        if (!content) {
            Swal.fire({
                customClass: {
                    container: 'my-swal'
                },
                text: msg,
                icon: 'error',
                confirmButtonText: ''
            });
            field.focus();
            return false;

        }
        return true;
    }

    /**
     *     .
     * @param {string} selector       css 
     * @param {int} len              
     * @param {string} msg              
     * @return {boolean}    true /   false 
     */
    max_length(selector, len, msg) {
        const field = document.querySelector(selector);
        const content = field.value.trim();
        if (content.length > len) {
            Swal.fire({
                customClass: {
                    container: 'my-swal'
                },
                text: msg,
                icon: 'error',
                confirmButtonText: ''
            });
            field.value = "";
            field.focus();
            return false;
        }
        return true;
    }

    /**
     *     .
     * @param {string} selector    css 
     * @param {int} len      
     * @param {string} msg      
     * @return {boolean}     true /   false  
     */
    min_length(selector, len, msg) {
        const field = document.querySelector(selector);
        const content = field.value.trim();
        if (content.length < len) {
            Swal.fire({
                customClass: {
                    container: 'my-swal'
                },
                text: msg,
                icon: 'error',
                confirmButtonText: ''
            });
            field.value = "";
            field.focus();
            return false;
        }
        return true;
    }

    /**
     *     .
     * @param {selector} selector    css 
     * @param {string} msg           
     * @return {boolean}   true /    false 
     */
    check(selector, msg) {
        const field = document.querySelectorAll(selector);
        let checked = false;

        // eslint-disable-next-line array-callback-return
        Array.from(field).some((v, i) => {
            if (v.checked) {
                checked = true;
                return true;
            }
        });

        if (!checked) {
            Swal.fire({
                customClass: {
                    container: 'my-swal'
                },
                text: msg,
                icon: 'error',
                confirmButtonText: ''
            });
            field[0].focus();
        }
        return checked;
    }

    /**
     *      .
     * @param {string} selector   css 
     * @param {int} min         
     * @param {string} msg           
     * @return {boolean}     true /    false
     */
    check_min(selector, min, msg) {
        let count = 0;
        const field = document.querySelectorAll(selector);

        field.forEach((v, i) => {
            if (v.checked) {
                count++;
            }
        });

        if (count < min) {
            Swal.fire({
                customClass: {
                    container: 'my-swal'
                },
                text: msg,
                icon: 'error',
                confirmButtonText: ''
            });
            field[0].focus();
            return false;
        }
        return true;
    }

    /**
     *      .
     * @param {string} selector    css
     * @param {int} max         
     * @param {string} msg           
     * @return {boolean}      true /    false
     */
    check_max(selector, max, msg) {
        let count = 0;
        const field = document.querySelectorAll(selector);

        field.forEach((v, i) => {
            if (v.checked) {
                count++;
            }
        });

        if (count > max) {
            Swal.fire({
                customClass: {
                    container: 'my-swal'
                },
                text: msg,
                icon: 'error',
                confirmButtonText: ''
            });
            field[0].focus();
            return false;
        }
        return true;
    }

    /**
     *     .
     * @param {string} origin_selector     selector
     * @param {string} compare_selector     selector
     * @param {string} msg           
     * @return {boolean}   true /   false
     */
    compare_to(origin_selector, compare_selector, msg) {
        const origin = document.querySelector(origin_selector);
        const compare = document.querySelector(compare_selector);
        let src = origin.value.trim();
        let dsc = compare.value.trim();

        if (src !== dsc) {
            Swal.fire({
                customClass: {
                    container: 'my-swal'
                },
                text: msg,
                icon: 'error',
                confirmButtonText: ''
            });
            origin.value = "";
            compare.value = "";
            origin.focus();
            return false;
        }
        return true;
    }

    /**
     *     .
     * @param {string} selector       css 
     * @param {string} msg          
     * @param {object} regex_expr    
     * @return {boolean}      true /    false
     */
    field(selector, msg, regex_expr) {
        const field = document.querySelector(selector);
        let src = field.value.trim();

        if (!src || !regex_expr.test(src)) {
            Swal.fire({
                customClass: {
                    container: 'my-swal'
                },
                text: msg,
                icon: 'error',
                confirmButtonText: ''
            });
            field.value = "";
            field.focus();
            return false;
        }
        return true;
    }

    /**
     *      field()  .
     * @param {string} selector   css  
     * @param {string} msg       
     * @return {boolean}    true /    false
     */
    num(selector, msg) {
        return this.field(selector, msg,  /^[0-9]*$/);
    }

    eng(selector, msg) {
        return this.field(selector, msg, /^[a-zA-Z]*$/);
    }

    kor(selector, msg) {
        return this.field(selector, msg, /^[--]*$/);
    }

    eng_num(selector, msg) {
        return this.field(selector, msg, /^[a-zA-Z0-9]*$/);
    }

    kor_num(selector, msg) {
        return this.field(selector, msg, /^[--0-9]*$/);
    }

    email(selector, msg) {
        return this.field(selector, msg, /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i);
    }

    cellphone(selector, msg) {
        return this.field(selector, msg, /^01(?:0|1|[6-9])(?:\d{3}|\d{4})\d{4}$/);
    }

    telephone(selector, msg) {
        return this.field(selector, msg, /^\d{2,3}\d{3,4}\d{4}$/);
    }

    phone(selector, msg) {
        let check1 = /^01(?:0|1|[6-9])(?:\d{3}|\d{4}\d{4}$)/;
        let check2 = /^\d{2,3}\d{3,4}\d{4}$/;

        const field = document.querySelector(selector);
        let src = field.value.trim();

        if (!src || (!check1.test(src) && !check2.test(src))) {
            Swal.fire({
                customClass: {
                    container: 'my-swal'
                },
                text: msg,
                icon: 'error',
                confirmButtonText: ''
            });
            field.value = "";
            field.focus();
            return false;
        }
        return true;
    }

    /**
     *     .
     * @param {int} nowDate 
     * @param {int} selectDate   
     * @param {string} msg     
     */
     isSameDate(nowDate, selectDate ,msg) {
        
        if (nowDate > selectDate) {
            Swal.fire({
                customClass: {
                    container: 'my-swal'
                },
                text: msg,
                icon: 'error',
                confirmButtonText: ''
            })
            return false;
        }
        return true;
    }
}

export default RegexHelper