import React, { useState } from 'react';
import axios from 'axios';
import Swal from "sweetalert2"

import {useDispatch } from "react-redux";

import LoginImage from "../../assets/img/LoginImage.png"

import Modal from '../Modal/Modal';
import Signin from './Signin';

import { getTokenIsOK } from '../../Slices/ReadTokenSlice'
// import setAuthorizationToken from './setAuthorizationToken';
import {Cookies} from 'react-cookie'

const cookies = new Cookies();


const Login = (props) => {
    const dispatch = useDispatch();
    

    // useState  open . (open true   )
    const [modalOpen, setModalOpen] = useState(false);
    
    const openModal = () => {
        setModalOpen(true);
    };
    const closeModal = () => {
        setModalOpen(false);
    };

    const { close } = props
    //   setUser 
    //   user      user      
    const [account, setAccount] = useState({
        id: "",
        pwd: "",
    });


    const onChangeAccount = (e) => {
        //...  account  
        //input     value   !
        //console.log(account)    account  !!
        setAccount({
            ...account,
            [e.target.name]: e.target.value,
        });
    };

    const onClickSubmit = () => {
        //   
        (async () => {
            try {
                const response = await axios.post(process.env.REACT_APP_LOCALHOST + "/members/login",
                    {
                        member_id: account.id,
                        member_pw: account.pwd
                    },{ withCredentials: true });
                const token = response.data.token;
                cookies.set("jwtToken", token)
                
                close()
            } catch (e) {
                Swal.fire({
                    customClass: {
                        container: 'my-swal'
                    },
                    text: JSON.parse(e.request.response).rtmsg,
                    icon: 'error',
                    confirmButtonText: ''
                })
            }
        })().then(() => {dispatch(getTokenIsOK())})
    }

    return (
        <div className="container">
            <p>LOGIN</p>
            <div className="flex-container">
                <img className="loginicon" src={LoginImage} alt="loginIcon" />
                <input onChange={onChangeAccount} name="id" placeholder="" />
                <input onChange={onChangeAccount} name="pwd" type="password" placeholder="" />
                <div className="btn-group">
                    <button className='Loginbutton' onClick={onClickSubmit}>LogIn</button>
                    <React.Fragment>
                        <button className='Loginbutton' onClick={openModal}>SignIn</button>
                        <Modal open={modalOpen} close={closeModal} header="SignIn">
                            <Signin close={closeModal} />
                        </Modal>
                    </React.Fragment>
                </div>
            </div>
        </div>
    );
};

export default Login;