import RegexHelper from "./regex_helper"

const SigninButton = () =>{
    const regexHelper = new RegexHelper();
    

    //  
    if (!regexHelper.value("#user_id", " .")) {
        return false;
    }
    if (!regexHelper.min_length("#user_id", 4, "  4   .")) {
        return false;
    }
    if (!regexHelper.max_length("#user_id", 20, "  20   .")) {
        return false;
    }
    if (!regexHelper.eng_num("#user_id", "     .")) {
        return false;
    }

    //  
    if (!regexHelper.value("#email", " .")) {
        return false;
    }
    if (!regexHelper.email("#email", "  .")) {
        return false;
    }

    //  
    if (!regexHelper.value("#user_pw", " .")) {
        return false;
    }
    if (!regexHelper.min_length("#user_pw", 4, "  4   .")) {
        return false;
    }
    if (!regexHelper.max_length("#user_pw", 20, "  20   .")) {
        return false;
    }
    if (!regexHelper.compare_to("#user_pw", "#user_pw_re", "  .")) {
        return false;
    }

    //  
    if (!regexHelper.value("#user_name", " .")) {
        return false;
    }
    if (!regexHelper.min_length("#user_name", 2, "  2   .")) {
        return false;
    }
    if (!regexHelper.max_length("#user_name", 20, "  20   .")) {
        return false;
    }
    if (!regexHelper.kor("#user_name", "   .")) {
        return false;
    }

    //  
    if (!regexHelper.value("#tel", " .")) {
        return false;
    }

    // if (!regexHelper.minusSign("#tel", " -  .")) {
    //     return false;
    // }

    if (!regexHelper.phone("#tel", " .")) {
        return false;
    }

    //  
    if (!regexHelper.value("#postcode", " .")) {
        return false;
    }
    if (!regexHelper.value("#address", " .")) {
        return false;
    }
    if (!regexHelper.value("#detailAddress", " .")) {
        return false;
    }

    //  
    if (!regexHelper.value("#user_date", " .")) {
        return false;
    }
}

export default SigninButton