import React, { useState } from 'react';
import DaumPostcodeEmbed from 'react-daum-postcode';
import { setPostCode } from "../../Slices/SignInPostCodeSlice"

//    hook action dispatch hook 
import { useDispatch } from "react-redux";

const KakaoAdress = () => {
    const [openPostcode, setOpenPostcode] = useState(false);

    // hook  slice   
    

    // dispatch  
    const dispatch = useDispatch();

    const [address, setAddress] = useState({
        postcode: "",
        longaddress: "",
        refaddress: "",
        detailaddress: ""
    })

    //
    const postCodeStyle = {
        width: "100%",
        height: "500px",
    }

    const handle = {
        //   
        clickButton: () => {
            setOpenPostcode(current => !current);
        },

        //   
        selectAddress: (data: any) => {
            //dispatch slice  
            dispatch(setPostCode({
                ...address,
                postcode: data.zonecode,
                longaddress: data.address,
                refaddress: data.bname,
            }))
            setAddress({
                postcode: data.zonecode,
                longaddress: data.address,
                refaddress: data.bname,
            })
            setOpenPostcode(false);
        },
    }

    const onChangeDetailAddress = (e) => {
        setAddress({
            ...address,
            detailaddress: e.target.value
        });

        dispatch(setPostCode({
            ...address,
            detailaddress: e.target.value
        }))
    }

    return (
        <div>
            <div className="form-group">
                <p className="ptag"></p>
                <div className="flex">
                    <div className="input-100">
                        <input value={address.postcode || ""} type="text" name="postcode" id="postcode" className="form-control justify" placeholder=""
                            readOnly />
                    </div>
                    <div className="input-100">
                        <button className='form-control' onClick={handle.clickButton}></button>
                    </div>
                </div>
                <div className="input-100">
                    <input value={address.longaddress || ""} type="text" name="address" id="address" className="form-control" placeholder="" readOnly />
                </div>
                <div className="flex">
                    <div className="input-100">
                        <input onChange={onChangeDetailAddress} type="text" name="detailAddress" id="detailAddress" className="form-control justify"
                            placeholder="" />
                    </div>
                    <div className="input-100">
                        <input value={address.refaddress || ""} type="text" name="extraAddress" id="extraAddress" className="form-control ref" placeholder=""
                            readOnly />
                    </div>
                </div>
                <div>
                    {openPostcode &&
                        <DaumPostcodeEmbed
                            onComplete={handle.selectAddress}  //     
                            autoClose={false} //     DOM    
                            style={postCodeStyle}
                        />}
                </div>
            </div>
        </div>
    )
};

export default KakaoAdress;