import React, { useState } from 'react';
import KakaoAdress from './KakaoAdress';
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import Swal from "sweetalert2"

import { setSignInfo } from "../../Slices/SignInDataSlice"
import SigninButton from "./SigninButton"

import "../../assets/css/style.min.css"

const Signin = () => {


    //    .....
    const { USER_POSTCODE, USER_ADDRESS, USER_DETAIL_ADDRESS } = useSelector((state) => state.SignInPostCode);
    const dispatch = useDispatch();
    const [info, getInfo] = useState({
        USER_ID: "",
        USER_EMAIL: "",
        USER_PASSWORD: "",
        USER_NAME: "",
        USER_TEL: "",
        USER_BIRTHDAY: ""
    })

    const getSignInfo = (e) => {
        getInfo({
            ...info,
            [e.target.name]: e.target.value,
        })
        dispatch(setSignInfo({
            ...info,
        }))
        
    }


    const onSubmit = () => {
        // 
        
        (async () => {
            try {
                SigninButton();
                await axios.post(process.env.REACT_APP_LOCALHOST +'/members/signin',
                    {
                        member_id: info.USER_ID,
                        member_email: info.USER_EMAIL,
                        member_pw: info.USER_PASSWORD,
                        member_name: info.USER_NAME,
                        member_phone: info.USER_TEL,
                        member_postcode: USER_POSTCODE,
                        member_addr1: USER_ADDRESS,
                        member_addr2: USER_DETAIL_ADDRESS,
                        member_birth: info.USER_BIRTHDAY,
                    });
                Swal.fire({
                    customClass: {
                        container: 'my-swal'
                    },
                    text: " !!",
                    icon: 'success',
                    confirmButtonText: ''
                }).then((result) => {
                    if (result.isConfirmed) {
                        window.location.href = "/"
                    }
                })
            } catch (e) {
                const rtmsg = JSON.parse(e.request.response).rtmsg

                if (rtmsg === "   .") {
                    Swal.fire({
                        customClass: {
                            container: 'my-swal'
                        },
                        text: rtmsg,
                        icon: 'error',
                        confirmButtonText: ''
                    })
                }

            }

        })();
        //     MySQL !!
        
    }

    return (
        <div className="join_form" >
            <h1> </h1>

            {/*  */}
            <div className="form-group">
                <p className="ptag"></p>
                <div className="input-100">
                    <input onChange={getSignInfo} type="text" name="USER_ID" id="user_id" className="form-control" placeholder=" " />
                </div>
            </div>

            {/*  */}
            <div className="form-group">
                <p className="ptag"></p>
                <div className="input-100">
                    <input onChange={getSignInfo} type="email" name="USER_EMAIL" id="email" className="form-control" placeholder=" " />
                </div>
            </div>

            {/*  */}
            <div className="form-group">
                <p className="ptag"></p>
                <div className="input-100">
                    <input onChange={getSignInfo} type="password" name="USER_PASSWORD" id="user_pw" className="form-control" placeholder=" " />
                </div>
                <div className="input-100">
                    <input type="password" name="user_pw_re" id="user_pw_re" className="form-control" placeholder=" " />
                </div>
            </div>

            {/*  */}
            <div className="form-group">
                <p className="ptag"></p>
                <div className="input-100">
                    <input onChange={getSignInfo} type="text" name="USER_NAME" id="user_name" className="form-control" placeholder=" " />
                </div>
            </div>

            {/*  */}
            <div className="form-group">
                <p className="ptag"></p>
                <div className="input-100">
                    <input onChange={getSignInfo} type="tel" name="USER_TEL" id="tel" className="form-control" placeholder="010-0000-0000" />
                </div>
            </div>

            {/*  */}
            <KakaoAdress />

            {/*  */}
            <div className="form-group">
                <p className="ptag"></p>
                <div className="input-100">
                    <input onChange={getSignInfo} type="date" name="USER_BIRTHDAY" id="user_date" className="form-control" />
                </div>
            </div>

            {/*   */}
            <div className="form-group">
                <div className="text-center">
                    <button onClick={onSubmit} className="SigninBtn" type="submit"></button>
                </div>
            </div>
        </div>
    );
};

export default Signin;